.containerTabla{
    font-size: 15px;
}
.home{
    height: 1000px;
}
.searchBar{
    margin-bottom: 1px ;
    margin-top: 10px;
    margin-left: 15%;
    justify-content: center;
    display: flex;
    border-color: #112D4E;
    background-color: #DBE2EF;
    width: 130px;
    height: 30px;
}
.table{
    table-layout: fixed;
    border-collapse: collapse;
    justify-content: center;
    margin-top: 30px;
    margin-left: 15%;
}
.tableHeader{
    border: 2px solid #112D4E;
    text-align: left;
    padding: 8px;
    background-color: #3F72AF ;
    color: #DBE2EF;
    width: fit-content;
    cursor: pointer;

}
.tableCell{
    border: 2px solid #112D4E;
    text-align: left;
    padding: 8px;
    background-color: #DBE2EF ;
    width: 120px;
}