.profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background-color: #f9f9f9;
  height: 100vh;
}

.profileHeader {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.profilePic {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-right: 20px;
}

.profile h2 {
  margin: 0;
}

.profileForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

  max-width: 400px;
}

.profile label {
  margin-bottom: 5px;
  font-size: 16px;
  font-weight: bold;
  text-align: left;
  width: 100%;
}

.profile input {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  line-height: 1.5;
  color: #333;
  background-color: #fff;
  transition: border-color 0.2s ease-in-out;
}

.profile input:focus {
  outline: none;
  border-color: #3f72af;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.profile button {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  color: #fff;
  background-color: #3f72af;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}

.profile button:hover {
  background-color: #112d4e;
}

.profile button:disabled {
  background-color: #dbe2ef;
  cursor: not-allowed;
}

.profile .errmsg {
  color: #dc3545;
  font-size: 14px;
  font-weight: bold;
  margin-top: 10px;
}

@media (max-width: 768px) {
  .profileForm {
    max-width: 100%;
  }
}

.profile input[title] {
  position: relative;
}

.profile input[title]::before {
  content: attr(title);
  position: absolute;
  bottom: 100%;
  left: 0;
  right: 0;
  padding: 5px;
  margin-bottom: 5px;
  font-size: 12px;
  line-height: 1.5;
  color: #fff;
  background-color: #3f72af;
  border-radius: 4px;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out;
}

.profile input[title]:hover::before {
  opacity: 1;
  visibility: visible;
}
